<template>
  <modal modalWidth="22em" :buttonColor="colorTheme">
    <template #content>
      <div class="input-name">Назва уроку:</div>
      <input-text v-model='lessonName' ref='lessonName'/>
      <div class="input-name">Опис помилки:</div>
      <input-textarea v-model='reportText' placeholder="Опишіть помилку тут" ref='reportText'/>
    </template>
    <template #buttons>
      <button class="button-small" :style="{ background: colorTheme }" @click="report()">Надіслати</button>
    </template>
  </modal>
</template>

<script>
import controller from '@/api/controller'
import modal from '@/components/modal-basic.vue'
import inputText from '@/components/input-text.vue'
import inputTextarea from '@/components/input-textarea.vue'
export default {
  components: { modal, inputText, inputTextarea },
  data() { return {
    colorTheme: localStorage.getItem('colorTheme'),
    lessonName: localStorage.getItem('lessonName'),
    reportText: ''
  }},
  computed: {
    inputs() { return Object.keys(this.$refs).map(e => this.$refs[e]) },
  },
  methods: {
    async report() {
      this.inputs.map(e => e.v$.$touch())
      if (!this.inputs.some(e => e.v$.$error)) {
        await controller.report('Повідомлення про помилку на skywing.online', {
          lesson: this.lessonName, 
          message: this.reportText
        })
        this.$emit("update:visible", false)
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.input-name {
  margin-top: .75em;
}
</style>