<template>
  <paginationItem v-for="i in pageRange" :key='i' 
    @click2='$emit("update:modelValue", i)' ref='paginationItems'
    :label='i+1' :wings='[i != 0, i != pageMax-1]' 
    :color='colors[i]' 
    :shape='"square"' 
    :width='100/(Math.min(pageMax, pageLimit+1)) + "%"'/>
</template>

<script>
import paginationItem from '@/components/pagination-item.vue'
export default {
  components: { paginationItem },
  props: ['modelValue', 'pageMax', 'pageLimit', 'colors'],
  mounted() { this.$refs.paginationItems[0].click() },
  data() { return {
    page: 1,
  }},
  computed: {
    pageRange() {
      let { page, pageLimit, pageMax } = this
      let n = (pageLimit + 1)/2
      let pages = [...Array(pageMax).keys()]
      if (page < n) { return pages.slice(0, pageLimit+1) }
      else if (page > pageMax - n) { return pages.slice(pageMax-pageLimit-1, pageMax) }
      else { return pages.filter(i => Math.abs(i-page) < n) }
    }
  },
  watch: {
    modelValue(n) { this.$refs.paginationItems[n].click() }
  }
}
</script>