<template>
<div class="task-tab">

  <div class="task-answer-row" v-if="type == 'radio'">
    <span class='task-answer-letter' v-for='j in 5' :key='j'>{{ String.fromCharCode(1039 + j) }}</span>
  </div>

  <div class="task-answer-title" v-if="type == 'input'">Відповідь:</div>

  <div v-for='i in data.length' :key='i'>
  
    <slot :name="'label'+i"></slot>

    <div class="task-answer-row" v-if="type == 'radio'">
      <input v-for='j in 5' :key='j' type='radio' 
       :name='i'
       class='task-answer-cell' 
       :disabled='disabled' 
       v-model='dataModel[i-1]'
       :value='String.fromCharCode(1039 + j)'
       :style='{ borderColor: cellColor(i-1, j-1) }'> 
    </div>

    <div v-if="type == 'input'">
      <input type="text" 
       class='task-answer-input' 
       :disabled='disabled' 
       v-model='dataModel[i-1]'
       :style='{ borderColor: inputColor(i-1) }'>
       <div class="task-correct-answer" v-if="showCorrect">
        Правильна відповідь:&ensp;<b>{{ correctData[i-1] }}</b>
      </div>
    </div>

  </div>

</div>
</template>

<script>
import chartColumn from '../components/charts/chart-column.vue'
export default {
  components: { chartColumn },
  props: {
    data: {type: Array, required: true},
    correctData: {type: Array, required: false},
    type: {type: String, required: true, default: 'radio'},
    disabled: {type: Boolean, required: false, default: false},
    showCorrect: {type: Boolean, required: false, default: false},
  },
  computed: {
    dataModel: {
      get() { return this.data },
      set(val) { this.$emit('update:data',val) }
    },
  },
  methods: {
    cellColor(i, j) {
      if (!this.correctData) return "gray"
      else {
        if (this.correctData[i]==String.fromCharCode(1040 + j) && this.data[i]==String.fromCharCode(1040 + j)) return this.$green
        else if (this.showCorrect && this.correctData[i]==String.fromCharCode(1040 + j)) return this.$green+'aa'
        else if (this.data[i]==String.fromCharCode(1040 + j)) return this.$red
        else return "#0004"
      }
    },
    inputColor(i) {
      if (!this.correctData) return "gray"
      else return this.correctData[i]==this.data[i] ? this.$green : this.$red
    }
  }, 
}
</script>

<style lang = 'scss' scoped>
// * {border: 1px solid red}
@import "@/styles.scss";
.bar-wrapper {
  height: 4em;
  padding: 5px 0;
}
.task-tab {
  display: inline-block;
  padding: 1em 1em;
  border: 1px solid #0002;
  border-radius: 4px;
}
.task-answer-row {
  display: flex;
  width: 15em;
  margin: .25em 0;
  justify-content: space-around;
}
.task-answer-title {
  font-weight: bold;
}
.task-answer-letter {
  font-weight: bold;
}
.task-answer-cell {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  appearance: none;
  position: relative;
  width: 4.5ch;
  height: 4.5ch;
  border-style: solid;
  border-width: 2px;
  border-radius: 2px;
  font-size: 1em;
  opacity: .5;
  &:hover { opacity: 1 }
  &:checked {
    opacity: 1;
    &::before, &::after {
      content: "";
      position: absolute;
      background: #333;
      width: 4ch;
      height: 3px;
      border-radius: 2px;
    }
    &::before { transform: rotate(45deg); }
    &::after { transform: rotate(-45deg); }
  }
}
.task-answer-input {
  width: 15em;
  height: 4ch;
  margin-top: 1em;
  border: .15em solid #0003;
  border-radius: 2px;
  font-family: 'Roboto';
  text-indent: 10px;
  font-size: 1em;
  color: #404040;
  &:hover { border-color: #333a; }
}
.task-correct-answer {
  margin-top: .5em;
  font-size: .8em;
}
</style>